<template>
  <div @click="_xiecheng">_xiecheng</div>
</template>
<script type="text/ecmascript-6">
import axios from 'axios'
export default {
  created() {
    console.log('TEST')
  },
  data() {
    return {
      xiecheng: []
    }
  },
  methods: {
    _xiecheng() {
      axios.get(`/static/json/携程.json`).then((res) => {
        this.xiecheng = res.data
        this._filters()
      })
    },
    _filters() {
      console.log('筛选')
      axios.get(`/static/json/国际酒店城市.json`).then((res) => {
        console.log('国际酒店城市: ', res.data)
        let original = res.data
        let obj = {
          Label: 'R',
          CityList: []
        }
        original.forEach(element => {
          if (element.Label !== '热门') {
            element.CityList.forEach(elementChild => {
              let cnames = this.xiecheng.map(v => v.cname)
              let index = cnames.indexOf(elementChild.CityName_CN)
              if (index >= 0) {
                obj.CityList.push(elementChild)
              }
            })
          }
        })
        console.log('xiecheng: ', this.xiecheng.length, 'homsom: ', obj.CityList.length)
        console.log('obj: ', obj)
        console.log('objString: ', JSON.stringify(obj))
      })
    }
  }
}
</script>



